.page-footer {
  padding-top: 65px;
  margin-top: auto;
  background-color: #fafafa;

  &__logo-nav-border-wrapper {
    display: flex;
    justify-content: space-between;
    width: 74%;
    padding-bottom: 61px;
    margin: 0 auto;
  }

  &__logo-border-wrapper {
    width: 201px;
  }

  &__logo {
    margin-bottom: 53px;
  }

  &__logo .logo__name {
    color: #203c42;
  }

  &__prolongation {
    display: none;
  }

  &__nav-contacts-wrapper {
    display: flex;
    width: 70%;
  }

  &__title {
    padding: 0;
    margin: 0;
    margin-bottom: 26px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 15px;
    font-weight: normal;
    line-height: 16px;
    letter-spacing: 0.05em;
    color: #1b2d37;
    text-transform: uppercase;
  }

  @media (max-width: $tablet-width) {
    padding-top: 19px;

    &__logo-nav-border-wrapper {
      flex-direction: column;
      width: 100%;
      padding-bottom: 36px;
    }

    &__logo-border-wrapper {
      width: 100%;
      margin-right: 0;
      border-bottom: 1px solid rgba(98, 105, 98, 0.2);
    }

    &__logo-prolongation-social-wrapper {
      display: flex;
      // justify-content: space-between;
      align-items: center;
      width: 94%;
      padding-bottom: 25px;
      margin: 0 auto;
    }

    &__logo {
      margin-right: 104px;
      margin-bottom: 0;
    }

    &__prolongation {
      display: block;
      padding: 0;
      padding-top: 6px;
      margin: 0;
      margin-right: auto;
      font-size: 13px;
      font-weight: normal;
      line-height: 24px;
      color: rgba(27, 45, 55, 0.5);
      letter-spacing: 0.04em;
      text-transform: uppercase;
    }

    &__nav-contacts-wrapper {
      width: 93.5%;
      margin: 0 auto;
      padding-top: 38px;
    }

    &__title {
      margin-bottom: 25px;
    }
  }

  @media (max-width: $mobile-width) {
    padding-top: 46px;

    &__logo-nav-border-wrapper {
      padding-bottom: 0;
    }

    &__logo-prolongation-social-wrapper {
      flex-direction: column;
      align-items: center;
      width: 91%;
      padding-bottom: 54px;
    }

    &__logo {
      width: 216px;
      margin-right: 0;
      margin-bottom: 20px;
    }

    &__logo .logo__img {
      width: 52px;
      height: 52px;
      margin-left: 0;
    }

    &__prolongation {
      margin-right: 0;
      margin-bottom: 31px;
    }

    &__nav-contacts-wrapper {
      flex-direction: column;
      width: 100%;
      padding-top: 0;
    }

    &__title {
      position: relative;
      padding-top: 29px;
      padding-bottom: 27px;
      margin-bottom: 0;

      &::before, &::after {
        position: absolute;
        background-color: #c4c4c4;
      }

      &::before {
        content: "";
        width: 16px;
        height: 1px;
        top: 37px;
        right: 3px;
        transform: translateY(-50%);
      }

      &::after {
        width: 1px;
        height: 16px;
        top: 29px;
        right: 11px;
        transform: translateX(50%);
      }

      &--menu-close {
        &::after {
          content: "";
        }
      }
    }
  }
}
